import React, {useEffect, useState} from 'react';
import {Avatar, Button, CssBaseline, TextField, Link, Grid, Box, Typography, 
  makeStyles, createMuiTheme, ThemeProvider, Container} from '@material-ui/core';
import LockOutlinedIcon from '@material-ui/icons/LockOutlined';
import { Redirect } from 'react-router';

const useStyles = makeStyles((theme) => ({
  paper: {
    marginTop: theme.spacing(8),
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
  },
  avatar: {
    margin: theme.spacing(1),
    backgroundColor: theme.palette.secondary.main,
  },
  form: {
    width: '100%', // Fix IE 11 issue.
    marginTop: theme.spacing(3),
  },
  submit: {
    margin: theme.spacing(3, 0, 2),
  },
}));

export default function SignUp() {
  const classes = useStyles();
  const [details, setDetails] = useState({firstName:'', lastName:'', email:'', password:''});
  const [final, setFinal] = useState({firstName:'', lastName:'', email:'', password:''});
  const [redirect, setRedirect] = useState(false)

  useEffect(() => {
    const requestOptions = {
        method: 'POST',
        headers: { 'Content-Type': 'application/json', 'Accept': 'application/json'},
        body: JSON.stringify(final)
    };
    async function fetchMyAPI() {
      await fetch('/signup', requestOptions)
        .then(response => response.json())
        .then(data => {
          if(data.succeded){
            alert('ההרשמה בוצעה בהצלחה')
            setRedirect(redirect=> !redirect);
          }
          else{
            alert('כתובת המייל כבר קיימת במערכת')
          }
        })
    }
      fetchMyAPI()
    }, [final]);
  
  function handleChange(e){
    const {value, name} = e.target;

    setDetails(preValue => {
      if(name === 'firstName'){
        return {
          firstName: value,
          lastName: preValue.lastName,
          email: preValue.email,
          password: preValue.password
        }
      }
      if(name === 'lastName'){
        return {
          firstName: preValue.firstName,
          lastName: value,
          email: preValue.email,
          password: preValue.password
        }
      }
      if(name === 'email'){
        return {
          firstName: preValue.firstName,
          lastName: preValue.lastName,
          email: value,
          password: preValue.password
        }
      }
      if(name === 'password'){
        return {
          firstName: preValue.firstName,
          lastName: preValue.lastName,
          email: preValue.email,
          password: value
        }
      }
    })
  }

  function handleClick(e){
    e.preventDefault();
    setFinal(details);
  }

  const theme = createMuiTheme({
    direction: 'rtl',
  });

  return (
    <ThemeProvider theme={theme}> 
    <div dir="rtl">
    <Container component="main" maxWidth="xs">
      <CssBaseline />
      <div className={classes.paper}>
        <Avatar className={classes.avatar}>
          <LockOutlinedIcon />
        </Avatar>
        <Typography component="h1" variant="h5">
          הרשמה
        </Typography>
        <form className={classes.form}> 
          <Grid container spacing={2}>
            <Grid item xs={12} sm={6}>
              <TextField
                autoComplete="fname"
                name="firstName"
                variant="outlined"
                required
                fullWidth
                id="firstName"
                label="שם פרטי"
                autoFocus
                onChange={handleChange}
              />
            </Grid>
            <Grid item xs={12} sm={6}>
              <TextField
                variant="outlined"
                required
                fullWidth
                id="lastName"
                label="שם משפחה"
                name="lastName"
                autoComplete="lname"
                onChange={handleChange}
              />
            </Grid>
            <Grid item xs={12}>
              <TextField
                variant="outlined"
                required
                fullWidth
                id="email"
                label="כתובת אימייל"
                name="email"
                autoComplete="email"
                type="email"
                onChange={handleChange}
              />
            </Grid>
            <Grid item xs={12}>
              <TextField
                variant="outlined"
                required
                fullWidth
                name="password"
                label="סיסמא"
                type="password"
                id="password"
                autoComplete="current-password"
                onChange={handleChange}
              />
            </Grid>
          </Grid>
            <br></br> 
              <Button
                id="submit"
                type="submit"
                fullWidth
                variant="contained"
                color="primary"
                onClick={handleClick}>
                הירשם
              </Button>
              {redirect &&  <Redirect push to="/form" />}
            <Grid container justify="flex-end">
              <Grid item>
                <Link href="/" variant="body2">
                  כבר יש לך חשבון? לחץ כאן
                </Link>
              </Grid>
            </Grid>
        </form>
      </div>
      <Box mt={5}>
      </Box>
    </Container>
    </div>
    </ThemeProvider>
  );
}