import React, {useEffect, useState} from 'react';
import {Avatar, Button, CssBaseline, TextField, Grid, Box, Typography, 
  makeStyles, createMuiTheme, ThemeProvider, Container} from '@material-ui/core';
import LockOutlinedIcon from '@material-ui/icons/LockOutlined';
import { Redirect } from 'react-router';
import Link from 'react-router-dom/Link'

const useStyles = makeStyles((theme) => ({
  paper: {
    marginTop: theme.spacing(8),
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
  },
  avatar: {
    margin: theme.spacing(1),
    backgroundColor: theme.palette.secondary.main,
  },
  form: {
    width: '100%', // Fix IE 11 issue.
    marginTop: theme.spacing(1),
  },
  submit: {
    margin: theme.spacing(3, 0, 2),
  },
}));

export default function Login() {
    const classes = useStyles();
    const [details, setDetails] = useState({email:'', password:''})
    const [final, setFinal] = useState({email:'', password:''});
    const [redirect, setRedirect] = useState(false)

    function handleClick(e){
      e.preventDefault();
      setFinal(details);
    }

    useEffect(() => {
      const requestOptions = {
          method: 'POST',
          headers: { 'Content-Type': 'application/json', 'Accept': 'application/json'},
          body: JSON.stringify(final)
      };
      
      async function fetchMyAPI() {
        await fetch('/', requestOptions)
          .then(response => response.json())
          .then(data => {
            console.log(data);
            if(data.succeded){
              setRedirect(redirect=> !redirect);
            }
            else{
              alert('פרטי ההתחברות לא נכונים')
            }
          })
      }
        fetchMyAPI()
    }, [final]);

    function handleChange(e){
      const {value, name} = e.target;
  
      setDetails(preValue => {
        if(name === 'email'){
          return {
            email: value,
            password: preValue.password
          }
        }
        if(name === 'password'){
          return {
            email: preValue.email,
            password: value
          }
        }
      })
    }

    const theme = createMuiTheme({
      direction: 'rtl',
    });

  return (
   
    <ThemeProvider theme={theme}> 
    <div dir="rtl">
    <Container component="main" maxWidth="xs">
      <CssBaseline />
      <div className={classes.paper}>
        <Typography variant="h5">
          כלי ניהולי ותכנית עבודה בענף הבנייה
        </Typography>
        <br></br>
        <Avatar className={classes.avatar}>
          <LockOutlinedIcon />
        </Avatar>
        <Typography component="h1" variant="h6">
          התחברות
        </Typography>
        <form className={classes.form}>
          <TextField
            variant="outlined"
            margin="normal"
            required
            fullWidth
            id="email"
            label="כתובת מייל"
            name="email"
            onChange={handleChange}
            autoComplete="email"
            autoFocus
          />
          <TextField
            variant="outlined"
            margin="normal"
            required
            fullWidth
            name="password"
            label="סיסמא"
            type="password"
            id="password"
            onChange={handleChange}
            autoComplete="current-password"
          />
          <Button
            type="submit"
            fullWidth
            variant="contained"
            color="primary"
            className={classes.submit}
            onClick={handleClick}>
            התחבר
          </Button>
          {redirect &&  <Redirect push to="/form" />}
          <Grid container>
            <Grid item xs>
            </Grid>
            <Grid item>
              <Link to="/form" variant="body2">
                {"משתמש חדש? לחץ כאן"}
              </Link>
            </Grid>
          </Grid>
        </form>
      </div>
      <Box mt={8}>
      </Box>
    </Container>
    </div>
    </ThemeProvider>
  );
}