import React, {useState} from 'react';
import { makeStyles, CssBaseline, Paper, Stepper, Button, Step, Typography, 
    StepLabel} from '@material-ui/core';
import Step2 from './Step2';
import Step1 from './Step1';
import Review from './Review';
import {createMuiTheme, ThemeProvider } from '@material-ui/core/styles';

const useStyles = makeStyles((theme) => ({
  appBar: {
    position: 'relative',
  },
  layout: {
    width: 'auto',
    marginLeft: theme.spacing(2),
    marginRight: theme.spacing(2),
    [theme.breakpoints.up(600 + theme.spacing(2) * 2)]: {
      width: 800,
      marginLeft: 'auto',
      marginRight: 'auto',
    },
  },
  paper: {
    marginTop: theme.spacing(3),
    marginBottom: theme.spacing(3),
    padding: theme.spacing(2),
    [theme.breakpoints.up(600 + theme.spacing(3) * 2)]: {
      marginTop: theme.spacing(6),
      marginBottom: theme.spacing(6),
      padding: theme.spacing(3),
    },
  },
  stepper: {
    padding: theme.spacing(3, 0, 5),
  },
  buttons: {
    display: 'flex',
    justifyContent: 'flex-end',
  },
  button: {
    marginTop: theme.spacing(3),
    marginLeft: theme.spacing(1),
  },
}));


export default function Form() {
  const classes = useStyles();
  const [activeStep, setActiveStep] = useState(0);
  const [pred, setPred] = useState({days: 0, cost: 0})
  const steps = ['פרטי פרוייקט קודם', 'פרטי פרוייקט נוכחי', 'חישוב צפי'];
  
  function getStepContent(step) {
    switch (step) {
      case 0:
        return <Step1/>;
      case 1:
        return <Step2 updatePrediction={updatePrediction}/>;
      case 2:
        return <Review prediction={pred}/>;
      default:
        throw new Error('Unknown step');
    }
  }
  
  function updatePrediction(prediction){
    setPred(preValue=>{
      return {
        days: prediction.days, 
        cost: prediction.cost
      }
    });
    setActiveStep(2);
  }

  const handleBack = () => {
    setActiveStep(activeStep - 1);
  };

  const handleStep = () => {
    setActiveStep(1);
  }

  const theme = createMuiTheme({
    direction: 'rtl',
  });

  return (
    <ThemeProvider theme={theme}> 
    <div dir="rtl">
    <React.Fragment>
      <CssBaseline />
      <main className={classes.layout}>
        <Paper className={classes.paper}>
        {activeStep !== 2 && activeStep !== 1 
            && <Typography component="h1" variant="h5" align="center">
            על מנת שנוכל להתבסס על פרויקטים שביצעת בעבר נשמח שתזין מספר נתונים : 
            </Typography>
        }
        {activeStep !== 0 && activeStep !== 2 
            && <Typography component="h1" variant="h5" align="center">
          בואו נתחיל ! עליך להזין את הנתונים הרלוונטיים עבור הפרויקט שלך : 
          </Typography>
        }
        <Button variant="contained"
                    color="primary"
                    onClick={handleStep}
                    className={classes.button}>
                      דלג ישר להזנת פרוייקט חדש
                    </Button>
          
          <Stepper activeStep={activeStep} className={classes.stepper}>
            {steps.map((label) => (
              <Step key={label}>
                <StepLabel>{label}</StepLabel>
              </Step>
            ))}
          </Stepper>
          <React.Fragment>
            {activeStep === steps.length ? (
              <React.Fragment>
                <Typography variant="h5" gutterBottom>
                  Thank you for your order.
                </Typography>
                <Typography variant="subtitle1">
                  Your order number is #2001539. We have emailed your order confirmation, and will
                  send you an update when your order has shipped.
                </Typography>
              </React.Fragment>
            ) : (
              <React.Fragment>
                {getStepContent(activeStep)}
                <div className={classes.buttons}>
                  {activeStep !== 0 && (
                    <Button onClick={handleBack} className={classes.button}>
                      הקודם
                    </Button>
                  )}
                  {/* <Button
                    variant="contained"
                    color="primary"
                    onClick={handleNext}
                    className={classes.button}
                  >
                    {activeStep === steps.length - 1 ? 'Place order' : 'הבא'}
                  </Button> */}
                </div>
              </React.Fragment>
            )}
          </React.Fragment>
        </Paper>
      </main>
    </React.Fragment>
    </div>
    </ThemeProvider>
  );
}